<template>
  <div class="form-wrapper">
    <v-form>
      <base-field-input
        v-model="form.values.name"
        label="Name"
      />

      <base-field-input
        mask="+1 (###) ###-####"
        v-model="form.values.phone"
        label="Phone"
      />

      <base-field-input
        v-model="form.values.email"
        label="Email"
      />

      <div class="d-flex flex-wrap justify-space-between">
        <v-btn
          :to="{ name: $constants.ROUTE_NAMES.CHANGE_PASSWORD }"
          :width="$vuetify.breakpoint.mobile ? '100%' : 'auto'"
          :height="$vuetify.breakpoint.mobile ? 50 : 35"
          depressed
          color="btn-background-grey"
          class="change-password-link primary--text mt-2 ma-lg-0"
        >
          Change Password
        </v-btn>

        <v-btn
          :width="$vuetify.breakpoint.mobile ? '100%' : 100"
          :height="$vuetify.breakpoint.mobile ? 50 : 35"
          depressed
          color="primary"
          class="mt-2"
          :class="[ $vuetify.breakpoint.mobile ? 'font-weight-bold' : 'font-weight-normal' ]"
          :disabled="!isPasswordsEqual"
          @click="submit"
        >
          Save
        </v-btn>
      </div>
    </v-form>
  </div>
</template>

<script>
import BaseFieldInput from '@components/BaseFieldInput';
import { createNamespacedHelpers, mapState } from 'vuex';
import { identical } from 'ramda';
import * as constants from '@modules/dispatcher/constants';

const { mapActions } = createNamespacedHelpers('dispatcher');

export default {
  name: 'DispatcherProfileEdit',
  components: { BaseFieldInput },
  data: () => ({
    form: {
      values: {
        name: '',
        phone: '',
        email: '',
      },
      serverErrors: {},
      serverError: '',
    },
  }),
  computed: {
    ...mapState(['userId']),
    isPasswordsEqual() {
      return identical(this.form.dispatcherPassword, this.form.dispatcherConfirmPassword);
    },
  },
  created() {
    this.getDispatcherProfile({ id: this.userId })
      .then((data) => {
        this.form.values.name = data.name;
        this.form.values.phone = data?.phone.startsWith('+1') ? data.phone.slice(2) : data.phone;
        this.form.values.email = data.email;
      }).catch((errors) => {
        if (Array.isArray(errors)) {
          this.serverError = errors;
        } else {
          this.serverErrors = errors;
        }
      });
  },
  methods: {
    ...mapActions({
      setDispatcherForm: constants.actions.SET_DISPATCHER_PROFILE,
      getDispatcherProfile: constants.actions.GET_DISPATCHER_PROFILE,
    }),
    submit() {
      this.setDispatcherForm({ data: this.form.values })
        .then(() => this.$notificationSuccess('Your profile was successfully updated'))
        .catch(() => this.$notificationError('Your profile wasn\'t updated'));
    },
  },
};
</script>

<style lang="scss" scoped>
  .change-password-link {
    @media #{map-get($display-breakpoints, 'lg-and-up')}{
      position: absolute;
      top: 20px;
      right: 20px;
    }
  }
</style>
